.card {

	//Functionality
	perspective: 150rem;
	-moz-perspective: 150rem; 
	position: relative;
	height: 47rem;

	&__side {
		height: 47rem;
		transition: all .8s ease;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
		border-radius: 3px;
		overflow: hidden;
		box-shadow: 0 1.5rem 4rem rgba($color-black, .15);

		&--front{
			background-color: $color-white;

		}

		&--back {
			transform: rotateY(180deg);

			&--1 {
				background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark);
			}

			&--2 {
				background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
			}

			&--3 {
				background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark);
			}

		}
	}

	&:hover &__side--front {
		transform: rotateY(-180deg);
	}

	&:hover &__side--back {
		transform: rotateY(0);
	}

	//Front side styling
	&__picture {
		background-size: cover;
		height: 20rem;
		background-blend-mode: screen;
		-webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
		clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
		
		border-top-left-radius: 3px;
		border-top-right-radius: 3px;

		&--1{
			background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark), url(../../assets/nat-5.jpg);
		}

		&--2{
			background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark), url(../../assets/nat-6.jpg);
		}

		&--3{
			background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark), url(../../assets/nat-7.jpg);
		}
	}

	&__heading {
		font-size: 2.8rem;
		font-weight: 300;
		text-transform: uppercase;
		text-align: right;
		color: $color-white;
		position: absolute;
		top: 12rem;
		right: 2rem;
		width: 75%;
	}

	&__heading-span {
		padding: 1rem 1.5rem;
		-webkit-box-decoration-break: clone;
		box-decoration-break: clone;
		&--1{
			background-image: linear-gradient(to right bottom,
				rgba($color-secondary-light, .85),
				rgba($color-secondary-dark, .85));
		}

		&--2{
			background-image: linear-gradient(to right bottom,
				rgba($color-primary-light, .85),
				rgba($color-primary-dark, .85));
		}

		&--3{
			background-image: linear-gradient(to right bottom,
				rgba($color-tertiary-light, .85),
				rgba($color-tertiary-dark, .85));
		}
	}

	&__details {
		padding: 3rem;

		ul {
			list-style: none;
			width: 80%;
			margin: 0 auto;

			li {
				text-align: center;
				font-size: 1.5rem;
				padding: 1rem;

				&:not(:last-child){
					border-bottom: 1px solid #eee;
				}
			}
		}
	}

	//back side

	&__cta {
		width: 90%;
		position: absolute;
		top: 50%;
		left: 50%;
		text-align: center;

		transform: translate(-50%, -50%);
	}

	&__price-box{
		text-align: center;
		color: $color-white;
		margin-bottom: 8rem;

	}

	&__price-only {
		font-size: 1.4rem;
		text-transform: uppercase;
	}

	&__price-value {
		font-size: 6rem;
		font-weight: 100;
	}



	//For mobile devices........**********

	@media only screen and (max-width: 56.25em), only screen and (hover: none) {
		//Functionality
		height: auto;
		border-radius: 3px;
		background-color: $color-white;
		box-shadow: 0 1.5rem 4rem rgba($color-black, .15);


		&__side {
			height: auto;
			position: relative;
			box-shadow: none;

			&--back {
				transform: rotateY(0);
				-webkit-clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
				clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
			}
		}

		&:hover &__side--front {
			transform: rotateY(0);
		}

		&:hover &__side--back {
			transform: rotateY(0);
		}

		&__details{
			padding: 3rem;
		}

		//back side

		&__cta {
			width: 90%;
			position: relative;
			top: 0;
			left: 0;
			transform: translate(0);
			width: 100%;
			padding: 7rem 4rem 4rem 4rem;
		}

		&__price-box{
			margin-bottom: 3rem;

		}

		&__price-value {
			font-size: 4rem;
		}

	}
}